<template>
    <div>
        <loader :is-visible="isLoading"></loader>
        <section class="main-content">
            <div class="row">
                <div class="col-md-12 padding-t_8">
                    <div class="theme-primary partition-full">
                        <span class="p-name text-white">{{ $t('AddVendor') }}</span>
                        <span class="p-actions float-right">
                            <a class="p-action-btn text-white" @click="Close" title="Back to List"><em
                                    class="fa fa-angle-double-left"></em><br /><span>{{ $t('BacktoList') }} </span></a>
                        </span>
                    </div>
                </div>
            </div>
            <div class="border p-3">
                <Form ref="form">
                    <dynamic-form ref="AddVendor" :buttons="buttons" :schema="FormSchema">
                        <template v-slot:tgslot-Email="{ data }">
                            <Field name="Email" :rules="{ 'required': true }">
                                <input type="text" v-model="txtEmail" name="Email" class="form-control box-shadow bg-white"
                                    @change="validateEmail" :class="{ 'form-control': true, 'is-invalid': CheckEmailtab }">
                            </Field>

                            <span v-if="isInvalidEmail" class="invalid-feedback d-block"> {{ $t('EnterValidEmail') }}</span>
                            <span v-if="checkEmail" name="Email" class="invalid-feedback d-block"> {{ $t('EmailRequired')
                            }}</span>
                        </template>
                        <template v-slot:tgslot-Password="{ data }">
                            <Field name="Password" :rules="{ 'required': true }">
                                <input type="Password" v-model="txtPassword" name="Password"
                                    class="form-control box-shadow bg-white" @change="validatePassword"
                                    :class="{ 'form-control': true, 'is-invalid': checkPasswordtab }">
                            </Field>
                            <span v-if="isInvalidPassword" class="invalid-feedback d-block"> {{ $t('PasswordValidation')
                            }}</span>
                            <span v-if="checkPassword" name="Email" class="invalid-feedback d-block"> {{
                                $t('PasswordRequired') }}</span>
                        </template>
                        <template v-slot:tgslot-ConfirmPassword="{ data }">
                            <Field name="Password" :rules="{ 'required': true }">
                                <input type="Password" v-model="txtconfirmPassword" name="ConfirmPassword"
                                    class="form-control box-shadow bg-white" @change="validateConPassword"
                                    :class="{ 'form-control': true, 'is-invalid': checkConPasswordtab }">
                            </Field>
                            <span v-if="isConfirmPassword" class="invalid-feedback d-block"> {{ $t('PwdConfPwdMatching')
                            }}</span>
                            <span v-if="checkConPassword" name="Email" class="invalid-feedback d-block"> {{
                                $t('ConfirmPasswordRequired') }}</span>
                        </template>

                    </dynamic-form>
                    <div class="row flex-row-reverse mt-2">
                        <div class="col-lg-6 text-right">
                            <a href="javascript:;" v-on:click="SubmitData()" class="btn btn-success mr-2 addSalesOrder_a"
                                title="Save">
                                <em class="fa fa-save pr-2"></em>{{ $t('Save') }}
                            </a>
                            <a href="javascript:void(0)" class="btn btn-danger" title="Cancel" @click="Close">
                                <em class="fa fa-close pr-2"></em>{{ $t('Cancel') }}
                            </a>
                        </div>
                        <div class="col-lg-6">
                            <div class="py-2">
                                <small class="text-danger">{{ $t('MandatoryString') }}</small>
                            </div>
                        </div>
                    </div>
                </Form>
            </div>
        </section>
    </div>
</template>
<script>
import DataService from '../../../services/DataService'
export default {
    data() {
        return {
            isLoading: false,
            EditFunctionalArea: false,
            AddFunctionalArea: true,
            txtEmail: '',
            txtPassword: '',
            txtconfirmPassword: '',
            checkEmail: false,
            CheckEmailtab: false,
            checkPassword: false,
            checkPasswordtab: false,
            checkConPassword: false,
            checkConPasswordtab: false,
            IsEmailExist:true,
            isInvalidEmail: '',
            isInvalidPassword: '',
            FormSchema: [{
                layoutType: "triple",
                Data: [{
                    astype: "TextField",
                    label: this.$t('CompanyName'),
                    name: "CompanyName",
                    value: "",
                    validationRules: {
                        "required": true,
                        max: 100
                    },
                },
                {
                    astype: "TextField",
                    label: this.$t('FirstName'),
                    name: "FirstName",
                    value: "",
                    validationRules: {
                        "required": true,
                        max: 100
                    },
                },
                {
                    astype: "TextField",
                    label: this.$t('LastName'),
                    name: "LastName",
                    value: "",
                    validationRules: {
                        "required": true,
                        max: 100
                    },
                },
                ]
            },
            {
                layoutType: "triple",
                Data: [{
                    astype: "SlotField",
                    label: this.$t('Email'),
                    name: "Email",
                    value: "",
                    validationRules: {
                        "required": true,
                        max: 100
                    },
                },
                {
                    astype: "SlotField",
                    label: this.$t('Password'),
                    name: "Password",
                    value: "",
                    validationRules: {
                        "required": true,
                        max: 100
                    },
                },
                {
                    astype: "SlotField",
                    label: this.$t('ConfirmPassword'),
                    name: "ConfirmPassword",
                    value: "",
                    validationRules: {
                        "required": true,
                        max: 100
                    },
                },
                ]
            },
            {
                layoutType: "triple",
                Data: [{
                    astype: "SelectField",
                    label: this.$t('Country'),
                    name: "Country",
                    value: "",
                    config: {
                        options: [],
                        onChange: this.GetCountryName
                    },
                    validationRules: { "required": true },
                },
                {
                    astype: "TextField",
                    label: this.$t('State'),
                    name: "State",
                    value: "",
                    validationRules: {
                        "required": true,
                        max: 100
                    },
                },
                {
                    astype: "TextField",
                    label: this.$t('City'),
                    name: "City",
                    value: "",
                    validationRules: {
                        "required": true,
                        max: 100
                    },
                },
                ]
            },
            {
                layoutType: "triple",
                Data: [{
                    astype: "TextField",
                    label: this.$t('Address 1'),
                    name: "Address1",
                    value: "",
                    validationRules: {
                        "required": true,
                        max: 100
                    },
                },
                {
                    astype: "TextField",
                    label: this.$t('Address 2'),
                    name: "Address2",
                    value: "",
                    validationRules: {
                        "required": false,
                        max: 100
                    },
                },
                {
                    astype: "NumericField",
                    label: this.$t('ZipCode'),
                    name: "ZipCode",
                    value: "",
                    validationRules: {
                        "required": true,
                        max: 100
                    },
                },
                ]
            },
            {
                layoutType: "triple",
                Data: [{
                    astype: "NumericField",
                    label: this.$t('PhoneNumber'),
                    name: "PhoneNumber",
                    value: "",
                    validationRules: {
                        "required": true,
                        max: 100
                    },
                },
                ]
            },
            ],
            buttons: [{
                type: "submit",
                class: "btn btn-success",
                text: "<i class='fa fa-save pr-2'></i>Save",
                isVisible: false,

            },
            {
                type: "button",
                class: "btn btn-danger",
                text: "<i class='fa fa-close pr-2'></i>Cancel",
                isVisible: false,
                onClick: this.Close,
            },
            ],
            CountryName:'',
            companyId: null,
            userId: null,
            RequisitionId: 0
        }
    },
    created: async function () {
        var vm = this;
        await vm.ShowCountry();
    },
    methods: {
        ShowCountry: async function () {
            var vm = this;
            vm.CountryData = [];
            var params = `ids=&moduleName=&type=COUNTRIES&search=&refId=`
            await DataService.GetDDLData(params).then(function (response) {
                if (response.data.DATA.length > 0) {
                    vm.FormSchema[2].Data[0].config.options = response.data.DATA;
                    vm.CountryData = response.data.DATA;
                }
                vm.$refs.AddVendor.UpdateKeyValue();
            })
        },
        validateEmail: function () {
            var vm = this;
            vm.checkEmail = false;
            const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
            vm.isInvalidEmail = !emailRegex.test(this.txtEmail);
            vm.CheckEmailtab=vm.isInvalidEmail;
        },
        validatePassword: function () {
            var vm = this;
            vm.txtconfirmPassword = '';
            vm.checkPassword = false;
            const emailRegex = /^(?=.*[A-Za-z])(?=.*\d)(?=.*[@$!%*?&])[A-Za-z\d@$!%*?&]{6,}$/;
            vm.isInvalidPassword = !emailRegex.test(this.txtPassword);
            vm.checkPasswordtab=  vm.isInvalidPassword;
        },
        validateConPassword: function () {
            var vm = this;
            if (vm.txtconfirmPassword != vm.txtPassword) {
                vm.checkConPassword = false;
                vm.isConfirmPassword = true;
                vm.checkConPasswordtab=true;
            } else {
                vm.isConfirmPassword = false;
                vm.checkConPasswordtab=false;
            }
        },
        GetCountryName: function (x, y, id) {
            var vm = this;
                var filterEventName = {};
                filterEventName = y.config.OptionsForBind.filter(z => z.value == y.value);
                vm.CountryName = filterEventName[0].name;
        },
        SubmitData: async function() {
            var vm = this;
            vm.$refs.form.validate().then(async result => {
                if (vm.$refs.form.errors.Email.length > 0) {
                    vm.checkEmail = true;
                    vm.isInvalidEmail = false;
                    vm.CheckEmailtab =true;
                } else {
                    vm.checkEmail = false;
                    vm.CheckEmailtab =false;
                }
                if (vm.$refs.form.errors.Password.length > 0) {
                    vm.checkPassword = true;
                    vm.isInvalidPassword = false;
                    vm.checkPasswordtab=true;
                } else {
                    vm.checkPassword = false;
                    vm.checkPasswordtab=false;
                }
                if (vm.txtconfirmPassword =='') {
                    vm.checkConPassword = true;
                    vm.isConfirmPassword = false;
                    vm.checkConPasswordtab=true;
                } else {
                    vm.checkConPassword = false;
                }
                if (result) {
                    var url = `Email=${vm.txtEmail}`
                    await DataService.CheckEmail(url).then((response) => {
                        if(response.data.length>0){
                            vm.IsEmailExist=true;
                        }else{
                            vm.IsEmailExist=false;
                        }

                    });

                    if(vm.IsEmailExist==false){
                    var jsonobj = {
                        PackageId:'',
                        CompanyName: vm.FormSchema[0].Data[0].value,
                        FirstName: vm.FormSchema[0].Data[1].value,
                        LastName: vm.FormSchema[0].Data[2].value,
                        Email: vm.txtEmail,
                        Password: vm.txtPassword,
                        ConfirmPassword:vm.txtconfirmPassword,
                        CountryId: vm.FormSchema[2].Data[0].value,
                        State: vm.FormSchema[2].Data[1].value,
                        City: vm.FormSchema[2].Data[2].value,
                        Address1: vm.FormSchema[3].Data[0].value,
                        Address2: vm.FormSchema[3].Data[1].value,
                        ZipCode:vm.FormSchema[3].Data[2].value,
                        Phone: vm.FormSchema[4].Data[0].value, 
                        AccountStatus:'',
                        NetAmount:0,
                        CompDescription:'',
                        Price:0,
                        TimeZoneId:'',
                        TotalNoOfLicense:'',
                        PasswordSalt:'',
                        PaymentOption:'', 
                        BillingAnniversaryDate:'',
                        CompanyType : "Vendor",
                        BatchId: 0,
                        PackageQueryString:'',
                        IsClientToCa:'',
                        NoOfLicense:'',
                        OneTimePayment:'',
                        IsTrial:'',
                        ip: '',
                        Token:'',
                        SubscriptionId:0,
                        PaymentStatus:'',
                        Requestfrom:'',
                        NoOfUsers:'',
                        AddOnIds:0,
                        RefCode:'', 
                        TalygenAmount:'',
                        AgentId:'',
                        ParentCompanyId:'',
                        CustomizeAddOn:'',
                        AnniversaryDateList:'',
                        BillingScheduleType:'',
                        PackageName: '',
                        BillingAnniversaryDate:'',
                        ProNetAmount:0,
                        PackageWithLicense:'',
                        os:'',
                        CountryName:vm.CountryName,
                        BAddress1:vm.FormSchema[3].Data[0].value,
                        BAddress2:vm.FormSchema[3].Data[1].value,
                        BCountry: vm.FormSchema[2].Data[0].value,
                        BCountryName: vm.CountryName,
                        BCity:vm.FormSchema[2].Data[2].value,
                        BState: vm.FormSchema[2].Data[1].value,
                        BZipCode:vm.FormSchema[3].Data[2].value,
                        UserId:0
                    };
                    var params = JSON.stringify(jsonobj);
                    DataService.AddVendor(params).then((response) => {
                        if (response.data == 'success') {
                            vm.ShowAlert(vm.$t("AddedSuccess", [vm.$t("Vendor")]), "success", true, vm.$t("Alert"));
                            vm.Close();
                            vm.$parent.FetchData();
                        }
                        else {
                            vm.ShowAlert(vm.$t("AddedError", [vm.$t("Vendor")]), "failure", true, vm.$t("Alert"));
                            vm.Close();
                        }
                    }); 
                    }else{
                        vm.ShowAlert(vm.$t("EmailAlreadyExist"), "warning", true, vm.$t("Alert"));
                        return false;
                    }
                }
            })
        },
        Close: function () {
            this.$router.push({
                name: 'VendorListing'
            });
        }
    }
}
</script>
>