<template>
    <section>
        <loader :is-visible="isLoading"></loader>
        <tg-list :IsShowAction="false" ModuleName="HR" SubModuleCode="" :LegendArray="[]"
            :RenderRowActionMethod="renderActions" v-on:ActionButtonClick="actionButtonClick" :widgets="[]"
            IdentityColumn="VENDOR_ID" :listType="['List']" :ListData="VendorData" :HeaderData="Headers"
            :HeaderButtons="listheaderbuttons" :TotalRecords="TotalRecords" :SearchFields="leftSearchFields"
            IsSearchFieldsOnTop="true" SearchPosition="top" :ListDataCallBackFunction="FetchData" :showCheckBox="false"
            :SortExp="SortExp" :SortBy="SortBy" :NorecordfoundText="$t('NoRecordfound')"
            v-on:LeftsearchButtonClick="leftCommonSearch" v-on:PagerButtonClick="pagerMethod"
            v-on:HeaderButtonClick="commonHeaderButtonClick" v-on:SortdataButtonClick="sortdata">
        </tg-list>
        <AddVendor v-if="ShowAddVendor" :HeaderText="HeaderText"></AddVendor>
    </section>
</template>
<script>
import Schema from "./Schema"
import DataServices from "@/services/DataService";
import AddVendor from './AddVendor.vue'
export default {
    components: { AddVendor },
    data() {
        return {
            isLoading: false,
            ShowAddVendor: false,
            noRecords: '',
            Headers: [{
                "COLUMN_NAME": "VENDOR_NAME",
                "DATA_TYPE": "text",
                "SORTABLE": true,
                "VISIBLE": true,
                "DISPLAY_NAME": 'Vendor Name',
                "DISPLAY_ORDER": 1,

            },
            {
                "COLUMN_NAME": "email_id",
                "DATA_TYPE": "text",
                "SORTABLE": true,
                "VISIBLE": true,
                "DISPLAY_NAME": 'Email',
                "DISPLAY_ORDER": 2
            },
            {
                "COLUMN_NAME": "LOCATION_NAME",
                "DATA_TYPE": "Date",
                "SORTABLE": true,
                "VISIBLE": true,
                "DISPLAY_NAME": 'Location',
                "DISPLAY_ORDER": 3,
            },
            {
                "COLUMN_NAME": "VENDORSERVICES",
                "DATA_TYPE": "Date",
                "SORTABLE": true,
                "VISIBLE": true,
                "DISPLAY_NAME": 'Vendor Services',
                "DISPLAY_ORDER": 4,
            },
            {
                "COLUMN_NAME": "PROJECTNAME",
                "DATA_TYPE": "Date",
                "SORTABLE": true,
                "VISIBLE": true,
                "DISPLAY_NAME": 'Assign Project',
                "DISPLAY_ORDER": 5,
            },

            ],
            leftSearchFields: [{
                fieldName: 'Search',
                fieldType: "text",
                fieldSearchConditionName: [''],
                paramName:"search",
                isSearch: true,
                value: "",
                listOptions: []
            }],
            SortBy: '',
            SortExp: '',
            PageSize: "10",
            PageNumber: "1",
            TotalRecords: 0,
            CurrentPage: 1,
            TotalPages: 0,           
            searchCondition:'',
            VendorData: [],
            listheaderbuttons: Schema.buttons,
            listActions: Schema.listActions,
            VendorId: 0,
            HeaderText: ''
        }
    },
    created: async function () {
        var vm = this;
        await vm.FetchData();
    },
    updated() {
        const divElement = document.getElementsByClassName('theme-primary partition p-actions-expand')
        if (divElement[0] && divElement[0].lastChild.className != 'p-name text-white') {
            const spanElement = document.createElement('span')
            spanElement.className = "p-name text-white"
            spanElement.textContent = this.$t('VendorListing');

            divElement[0].appendChild(spanElement);
        }
    },
    methods: {
        AddVendor: function (VendorId, TextHeader) {
            this.VendorId = VendorId;
            this.HeaderText = TextHeader;
            this.ShowAddVendor = true;
            if(TextHeader=='AddVendor')
                this.$router.push({ name: 'AddVendor', params: { reqfrom: 'VendorUser/AddVendor', VendorId: VendorId} });
           
        },
        leftCommonSearch: function (value) {
            this.PageNumber = value.PageNumber;
            this.isPaged = 1;
            if (typeof value.searchCondition != 'undefined') {
                let str = value.searchConditionJson;
                let result = str.split('=')[1];
                this.searchCondition = result;
            }
            else {
                this.searchCondition = "";
            }
            this.FetchData();
        },
        commonHeaderButtonClick: function (event) {
            switch (event.callbackfunction) {
                case "AddVendor":
                    this.AddVendor(0, 'AddVendor');
                break;
            }
        },
        sortdata: function (s) {
            var SortDir = "sort tb_headerSortDown";
            this.SortBy = s;
            if (s === this.SortBy) {
                if (this.SortExp == "") {
                    this.SortExp = "ASC";
                    SortDir = "sort tb_headerSortDown";
                } else if (this.SortExp == "ASC") {
                    this.SortExp = "DESC";
                    SortDir = "sort tb_headerSortUp";
                } else {
                    this.SortExp = "ASC";
                    SortDir = "sort tb_headerSortDown";
                }
            }
            this.FetchData();
            $("#" + s + '  span:first-child').attr("class", SortDir);
        },
        pagerMethod: function (value) {            
            this.PageSize = value.PageSize;
            this.PageNumber = value.PageNumber;
            this.FetchData();
        },
        renderActions: function () { },
        FetchData: async function () {
            var vm = this;
            var url = `pageSize=${vm.PageSize}&pageNum=${vm.PageNumber}&sortBy=${vm.SortBy}&sortExp=${vm.SortExp}`
            if (!vm.isBlank(this.searchCondition)) {
                url += `&search=${this.searchCondition}`;
            }
            await DataServices.GetVendorListing(url).then((response) => {
                if (response.data && response.data.length > 0) {
                    vm.VendorData = response.data;                
                    vm.TotalRecords = response.data[0].TOTAL_RECORD;
                    vm.TotalPages = Math.ceil(vm.TotalRecords / vm.PageSize);
                    vm.CurrentPage = vm.PageNumber;
                    vm.noRecords = false;
                } else {
                    vm.VendorData = [];
                    vm.TotalRecords = 0
                    vm.noRecords = true;
                }
            })
        }
    }
}
</script>
